export const g = 9.8

// 实用堰类型
export enum syyType {
    syy, // 实用堰
    kdy // 宽顶堰
}

// 形状类型
export enum shapeType {
    RECTANGLE,
    LADDER
}

// 泄槽水面线SGD
export function eqfm(h: number, theta: number, alpha: number, qc: number, bc: number) {
  let v = qc / (bc * h)
  return h * Math.cos(theta) + alpha * v * v / (2 * g)
}

export function ssfl_sgd(sType: shapeType, m: number, learning_rate: number, lastH: number,
  bc: number, qc: number, n: number, theta: number,
  i: number, alpha: number, deltaL: number,
  precision: number) {
  let h = lastH
  let omega = 0
  let x = 0

  if (sType == shapeType.RECTANGLE) {
    omega = bc * h
    x = (bc + 2 * h)
  } else if (sType == shapeType.LADDER) {
    omega = (bc + m * h) * h
    x = (bc + 2 * h * Math.sqrt(1 + m * m))
  }

  let lastR = omega / x
  let lastv = qc / omega
  let lastC = Math.pow(lastR, 1 / 6) * 1 / n

  let min_Val = 99999
  let min_h = 0
  let flag = true
  for (let ii = 0; ii < 10000; ii++) {
    if (sType == shapeType.RECTANGLE) {
      omega = bc * h
      x = (bc + 2 * h)
    } else if (sType == shapeType.LADDER) {
      omega = (bc + m * h) * h
      x = (bc + 2 * h * Math.sqrt(1 + m * m))
    }

    let R = omega / x
    let v = qc / omega
    let C = Math.pow(R, 1 / 6) * 1 / n
    let aver_v = (lastv + v) / 2
    let aver_R = (lastR + R) / 2
    let aver_C = (lastC + C) / 2
    let J = aver_v * aver_v / (aver_R * aver_C * aver_C)
    let rightValue = ((eqfm(lastH, theta, alpha, qc, bc) + deltaL * (i - J)) - (alpha * v * v / (2 * g))) / (Math.cos(theta))
    h -= learning_rate * Math.abs(h - rightValue)

    let chkValue = Math.abs(Math.abs((eqfm(h, theta, alpha, qc, bc) - eqfm(lastH, theta, alpha, qc, bc)) / (i - J)) - deltaL)

    if (chkValue < min_Val && flag) {
      min_Val = chkValue
      min_h = h
    } else {
      flag = false
    }

    if (chkValue < precision) {
      // success
      return h
    }
  }

  // console.log("精度无法达到,当前精度为:" + min_Val);
  // console.log("h为:" + min_h);
  return min_h
}

// 侧槽断水面线sgd
export function ccssm_sgd(n_z: number, b_s: number, b_m: number, Q: number, L: number, N: number, s_n: number, delta: number,
  lastH: number, lastv: number, lastR: number, lastQ: number, delta_H: number) {
  let learning_rate = 0.01
  let bn = b_s + (b_m - b_s) / L * (L - (s_n - 1) * L / (N - 1))
  let Qn = Q - (Q / L) * (s_n - 1) * (L / (N - 1))

  let _delta_H = delta_H
  for (let ii = 0; ii < 10000; ii++) {
    let hn = lastH + _delta_H - delta
    let vn = Qn / (bn * hn)
    let Rn = bn * hn / (bn + 2 * hn)
    let aver_v = (lastv + vn) / 2
    let aver_R = (lastR + Rn) / 2
    let aver_J = aver_v * aver_v * n_z * n_z / (Math.pow(aver_R, 4 / 3))
    let _deltaH = (lastQ * (lastv + vn) / (g * (lastQ + Qn))) * ((lastv - vn) + vn * (lastQ - Qn) / lastQ) + aver_J * L / (N - 1)
    let _hn = lastH + _deltaH - delta
    _delta_H -= learning_rate * (_delta_H - _deltaH)
    let chk = Math.abs(_hn - hn)
    if (chk < 0.001) {
      // console.log("_______________________");
      // console.log("当前断面n下标:" + s_n);
      // console.log("delta_H:" + delta_H);
      // console.log("H:" + hn);
      return [hn, _delta_H, vn, Rn, Qn]
    }
  }

  return [-1]
}
