











































































































































import { Component, Vue, Prop } from 'vue-property-decorator'
import VueParamaters from '@/components/VueParamaters/index.vue'
import MathJax3 from '@/components/MathJax3/index.vue'
import { syyType, shapeType } from '@/hycom_lib/common'
import { getToken } from '@/utils/auth'

@Component({
  components: {
    VueParamaters,
    MathJax3
  }
})
export default class HycomForm extends Vue {
  @Prop() title!: string;
  @Prop() data!: Object;
  @Prop() initData!: Object;
  @Prop() result!: string;

  @Prop() demoContent!: string;
  @Prop() demoResult!: string;
  @Prop() formulas!: Object;

  private form = { Q: '' };
  private syyOptions = [
    {
      label: '实用堰',
      value: syyType.syy
    },
    {
      label: '宽顶堰',
      value: syyType.kdy
    }
  ];

  private shapeOptions = [
    {
      label: '矩形断面',
      value: shapeType.RECTANGLE
    },
    {
      label: '梯形断面',
      value: shapeType.LADDER
    }
  ];

  private onDemo() {
    for (let key in this.data) {
      (this.data as any)[key] = (this.initData as any)[key]
    }
  }

  private onReset() {
    for (let key in this.data) {
      if (typeof (this.data as any)[key] === 'boolean') {
        (this.data as any)[key] = false
      } else {
        (this.data as any)[key] = ''
      }
    }
  }

  private onCalculate() {
    this.form.Q = this.result
  }

  private CheckToken() {}
}
