import { g, eqfm } from './common'
/**
 * Chapter2 水工隧洞水力学计算
 * 部分计算参考以下网址
 * https://raymondmcguire.github.io/?e=HYCAL&&theme=Image-Visual-Lab
 */
export module Chapter2 {

    /**
     * 2.1水工有压隧洞的水力学计算
     * 2.1.1水工有压隧洞流量的计算
     * @param miu 流量系数
     * @param a 出口断面洞高，m
     * @param b
     * @param T_0 上游水面与隧洞出口底板高程差及上游行进流量水头之和
     * @param H_p 隧洞出口断面水流的平均单位势能
     * @returns Q
     */
    export function sgyysdlljs(miu: number, a: number, b: number, T_0: number, H_p: number) {
      let Q = miu * a * b * Math.sqrt(2 * g * (T_0 - H_p))
      return Q
    }

    /**
     * 2.1水工有压隧洞的水力学计算
     * 2.1.2有压短管的无压隧洞泄量计算
     * @param miu 流量系数
     * @param B
     * @param e
     * @param H
     * @param eplison
     * @returns Q
     * @returns v
     */
    export function yydgdwysdxlljs(miu: number, B: number, e: number, H: number, eplison: number) {
      let Q = miu * B * e * Math.sqrt(2 * g * (H - eplison * e))
      let v = Q / (B * e)
      return [Q, v]
    }

    /**
     * 2.2水面线计算
     * @param Q_c
     * @param b_c
     * @param b_c_1
     * @param n_z
     * @param i
     * @param e
     * @param eplison
     * @param deltaL
     * @returns Q
     */
    export function sgysdsmxjs(Q_c: number, b_c: number, b_c_1: number, n_z: number, i: number, e: number, eplison: number, deltaL: number) {
      let h_k = Math.pow((Q_c * Q_c) / (b_c * b_c * g), 1 / 3)
      let R_k = b_c * h_k / (b_c + 2 * h_k)
      let C_k = 1 / n_z * Math.pow(R_k, 1 / 6)
      let i_k = ((Q_c * Q_c) / (b_c * b_c)) / (h_k * h_k * C_k * C_k * R_k)

      let h_0 = e * eplison
      let theta = Math.atan(i)
      let lastR = (b_c_1 * h_0) / (b_c_1 + 2 * h_0)
      let lastv = Q_c / (b_c_1 * h_0)
      // console.log(h_0);
      let h = h_0
      let learning_rate = 0.001
      let precision = 0.001
      if (i > i_k) {
        // 急流
        for (let ii = 0; ii < 10000; ii++) {
          let R = (b_c * h) / (b_c + 2 * h)
          let v = Q_c / (b_c * h)
          let aver_v = (lastv + v) / 2
          let aver_R = (lastR + R) / 2
          let J = n_z * n_z * aver_v * aver_v / Math.pow(aver_R, 4 / 3)
          let rightValue = deltaL - ((eqfm(h, theta, 1, Q_c, b_c) - eqfm(h_0, theta, 1, Q_c, 8)) / (i - J))

          // console.log(h);
          let chkValue = Math.abs(rightValue)
          if (chkValue < precision) {
            // success
            // console.log(h);
            // console.log(eqfm(h_0, theta, 1, Q_c, 8));
            return h
          }

          h -= learning_rate * rightValue
        }

        return -1
      } else {
        // 缓流
        return -1
      }
    }

    export function sgysdsmxjs_2(Q_c: number, b_c: number, n_z: number, i: number, h_1: number, deltaL: number) {
      let theta = Math.atan(i)
      let lastR = (b_c * h_1) / (b_c + 2 * h_1)
      let lastv = Q_c / (b_c * h_1)
      // console.log(lastR,lastv);
      let h = h_1
      let learning_rate = 0.001
      let precision = 0.001

      for (let ii = 0; ii < 10000; ii++) {
        let R = (b_c * h) / (b_c + 2 * h)
        let v = Q_c / (b_c * h)
        let aver_v = (lastv + v) / 2
        let aver_R = (lastR + R) / 2
        let J = n_z * n_z * aver_v * aver_v / Math.pow(aver_R, 4 / 3)
        let rightValue = deltaL - ((eqfm(h, theta, 1, Q_c, b_c) - eqfm(h_1, theta, 1, Q_c, b_c)) / (i - J))

        // console.log(h);
        let chkValue = Math.abs(rightValue)
        if (chkValue < precision) {
          // success
          // console.log(h);
          // console.log(eqfm(h_0, theta, 1, Q_c, 8));
          return h
        }

        h += learning_rate * rightValue
      }

      return -1
    }
}
